import { Auth } from "../../api";

let initialState = {
  login: {
    open: false,
    message: "",
    severity: "",
  },

  registration: {
    open: false,
    message: "",
    severity: "",
  },

  answer_verify: {
    open: false,
    message: "",
    severity: "",
  },

  code_data: {
    open: false,
    message: "",
    severity: "",
  },
};

const login = (state = initialState, action) => {
  switch (action.type) {
    case "SEND_LOGIN_DATA": {
      return {
        ...state,
        login: action.data,
      };
    }

    case "SEND_REGISTRATION_DATA": {
      return {
        ...state,
        registration: action.data,
      };
    }

    case "ANSWER_VERIFY": {
      return {
        ...state,
        answer_verify: action.data,
      };
    }

    case "SEND_CODE": {
      return {
        ...state,
        code_data: action.data,
      };
    }

    default:
      return state;
  }
};

export const setLoginData = (data) => ({ type: "SEND_LOGIN_DATA", data });
export const setRegData = (data) => ({ type: "SEND_REGISTRATION_DATA", data });
export const setCodeData = (data) => ({ type: "SEND_CODE", data });
export const setAnswerVerify = (data) => ({ type: "ANSWER_VERIFY", data });

export const postLoginData = (data) => {
  return (dispatch) => {
    Auth.postLogin(data).then((response) =>
      dispatch(
        setLoginData({
          open: true,
          message: response.data,
          severity: response.status,
        })
      )
    );
  };
};

export const sendRegData = (data) => {
  return (dispatch) => {
    Auth.sendRegData(data).then((response) =>
      dispatch(
        setRegData({
          open: true,
          message: response.data,
          severity: response.status,
        })
      )
    );
  };
};

export const sendCode = (data) => (dispatch) =>
  Auth.sendCode(data).then((response) =>
    dispatch(
      setCodeData({
        open: true,
        message: response.data,
        severity: response.status,
      })
    )
  );

export const sendCodeToVerify = (data) => (dispatch) =>
  Auth.verifyCode(data).then((response) =>
    dispatch(
      setAnswerVerify({
        open: true,
        message: response.data,
        severity: response.status,
      })
    )
  );

export const resetPassword = (data) => (dispatch) =>
  Auth.resetPassword(data).then((response) =>
    dispatch(
      setAnswerVerify({
        open: true,
        message: response.data,
        severity: response.status,
      })
    )
  );

export const sendDataToResetPhone = (data) => (dispatch) =>
  Auth.sendDataToResetPhone(data).then((response) =>
    dispatch(
      setAnswerVerify({
        open: true,
        message: response.data,
        severity: response.status,
      })
    )
  );

export default login;
