import { Account } from "../../api";
const GET_BONUS_DATA = "GET_BONUS_DATA";
const GET_REGIONS_DATA = "GET_REGIONS_DATA";

let initialState = {
  account: {},
  editedAccountData: {},
  bonus: {},
  regions: {},
};

const account = (state = initialState, action) => {
  switch (action.type) {
    case "GET_ACCOUNT_DATA": {
      return {
        ...state,
        account: action.data,
      };
    }
    case "SET_ACCOUNT_DATA": {
      return {
        ...state,
        editedAccountData: action.data,
      };
    }

    case GET_BONUS_DATA: {
      return {
        ...state,
        bonus: action.data,
      };
    }

    case GET_REGIONS_DATA: {
      return {
        ...state,
        regions: action.data,
      };
    }

    default:
      return state;
  }
};

export const setAccountData = (data) => ({ type: "GET_ACCOUNT_DATA", data });
export const setEditData = (data) => ({ type: "SET_ACCOUNT_DATA", data });
export const setBonusData = (data) => ({ type: GET_BONUS_DATA, data });
export const setRegionsData = (data) => ({ type: GET_REGIONS_DATA, data });

export const getAccountData = () => {
  return (dispatch) => {
    Account.getData().then((response) => dispatch(setAccountData(response)));
  };
};

export const editData = (values) => {
  return (dispatch) => {
    Account.editData(values).then((response) =>
      dispatch(
        setEditData({
          open: true,
          data: response.data,
          severity: response.status,
        })
      )
    );
  };
};

export const getBonusData = () => {
  return (dispatch) => {
    Account.getData().then((response) => dispatch(setBonusData(response)));
  };
};

export const getRegionsData = () => {
  return (dispatch) => {
    Account.getRegionsData().then((response) => dispatch(setRegionsData(response)));
  };
};

export default account;
