import axios from "axios";
import cookie from "cookie_js";

function get_token() {
  return cookie.get("token");
}

export const instance = axios.create({
  baseURL: `https://api.doce.kg`,
  headers: {
    Accept: "application/json",
    "Content-Type": "application/json; charset=UTF-8",
    Authorization: `Bearer ${get_token()}`,
  },
});

export const instanceNotAuthorized = axios.create({
  baseURL: `https://api.doce.kg`,
  headers: {
    Accept: "application/json",
    "Content-Type": "application/json; charset=UTF-8",
  },
});

export const Auth = {
  postLogin(data) {
    return instance
      .post("/account/token/", data)
      .then((response) => {
        return response;
      })
      .catch((error) => {
        return error.response;
      });
  },

  sendRegData(data) {
    return instanceNotAuthorized
      .post("/account/register/", data)
      .then((response) => {
        return response;
      })
      .catch((error) => {
        return error.response;
      });
  },

  sendCode(data) {
    return instanceNotAuthorized
      .post("/account/code/send/", data)
      .then((response) => {
        return response;
      })
      .catch((error) => {
        return error.response;
      });
  },

  sendCodeAuth(data) {
    return instance
      .post("/account/code/send/", data)
      .then((response) => {
        return response;
      })
      .catch((error) => {
        return error.response;
      });
  },

  verifyCode(data) {
    return instanceNotAuthorized
      .post("/account/code/verify/", data)
      .then((response) => {
        return response;
      })
      .catch((error) => {
        return error.response;
      });
  },

  resetPassword(data) {
    return instanceNotAuthorized
      .post(`/account/password/verify/reset/`, data)
      .then((response) => {
        return response;
      })
      .catch((error) => {
        return error.response;
      });
  },

  sendDataToResetPhone(data) {
    return instance
      .post("/account/phone/verify/reset/", data)
      .then((response) => {
        return response;
      })
      .catch((error) => {
        return error.response;
      });
  },
};

export const Account = {
  getData() {
    return instance
      .get("/account/get/")
      .then((response) => {
        return response.data;
      })
      .catch((error) => {
        return error.response;
      });
  },

  editData(values) {
    return instance
      .patch("/account/update/info/", values)
      .then((response) => {
        return response;
      })
      .catch((error) => {
        return error.response;
      });
  },
  getRegionsData() {
    return instanceNotAuthorized
      .get("/account/regions/")
      .then((response) => {
        return response;
      })
      .catch((error) => {
        return error.response;
      });
  },
};

