import {
  Alert,
  Button,
  InputAdornment,
  Snackbar,
  TextField,
  Typography,
} from "@mui/material";
import { useFormik } from "formik";
import React, { useLayoutEffect, useRef, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link, useNavigate } from "react-router-dom";
import { resetPassword, setAnswerVerify } from "../../../redux/reducers/auth";
import * as Yup from "yup";
import AuthContainer from "../AuthContainer";
import LockIcon from "../LockIcon";
import VisibilityOffIcon from "@mui/icons-material/VisibilityOff";
import VisibilityIcon from "@mui/icons-material/Visibility";

const EnteringCode = ({ setLoading }) => {
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const { answer_verify } = useSelector(({ auth }) => auth);

  const [visible, setVisible] = useState(false);

  const validationSearch = Yup.object().shape({
    passwordConfirmation: Yup.string().oneOf(
      [Yup.ref("password"), null],
      "Пароли должны совпадать"
    ),
  });

  const formik = useFormik({
    initialValues: {
      code: "",
      password: "",
    },
    validationSchema: validationSearch,
    onSubmit: (values) => {
      setLoading(true);
      dispatch(resetPassword(values));
    },
  });

  const firstUpdate = useRef(true);

  useLayoutEffect(() => {
    if (firstUpdate.current) {
      firstUpdate.current = false;
      return;
    }

    // hide linear progress
    setLoading(false);

    if (answer_verify.severity === 202) {
      setTimeout(() => {
        navigate("/auth/login");
        dispatch(setAnswerVerify({ open: false, severity: 0 }));
      }, 1000);
    }
  }, [answer_verify]);

  return (
    <AuthContainer>
      <>
        <Snackbar
          open={answer_verify.open}
          autoHideDuration={2000}
          anchorOrigin={{ vertical: "top", horizontal: "center" }}
          onClose={() =>
            dispatch(
              setAnswerVerify({
                ...answer_verify,
                open: false,
              })
            )
          }
        >
          <Alert
            severity={answer_verify.severity === 202 ? "success" : "error"}
            variant="filled"
          >
            {/* {answer_verify.message && Object.values(answer_verify?.message)[0]} */}
          </Alert>
        </Snackbar>
        <form onSubmit={formik.handleSubmit}>
          <Typography variant="h6" mb={4}>
            Восстановить пароль
          </Typography>
          <Typography
            variant="body2"
            mb={4}
            sx={{
              lineHeight: "150%",
            }}
          >
            Введите код, который был отправлен на ваш номер и ваш новый пароль
          </Typography>
          <TextField
            variant="outlined"
            name="code"
            fullWidth
            value={formik.values.code}
            onChange={formik.handleChange}
            placeholder="Код"
            sx={{ mb: 2 }}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <LockIcon />
                </InputAdornment>
              ),
            }}
          />
          <TextField
            required
            fullWidth
            name="password"
            value={formik.values.password}
            onChange={formik.handleChange}
            type={visible ? "text" : "password"}
            variant="outlined"
            helperText={formik.errors.password}
            placeholder="Пароль"
            sx={{ mb: 2 }}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <LockIcon />
                </InputAdornment>
              ),
              endAdornment: (
                <InputAdornment
                  position="end"
                  onClick={() => setVisible(!visible)}
                >
                  {!visible ? <VisibilityOffIcon /> : <VisibilityIcon />}
                </InputAdornment>
              ),
              inputProps: {
                pattern: ".{6,}",
              },
            }}
          />

          <TextField
            required
            fullWidth
            name="passwordConfirmation"
            value={formik.values.passwordConfirmation}
            type={visible ? "text" : "password"}
            error={formik.errors.passwordConfirmation}
            onChange={formik.handleChange}
            helperText={formik.errors.passwordConfirmation}
            variant="outlined"
            placeholder="Повторите пароль"
            sx={{ mb: 2 }}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <LockIcon />
                </InputAdornment>
              ),
              endAdornment: (
                <InputAdornment
                  position="end"
                  onClick={() => setVisible(!visible)}
                >
                  {!visible ? <VisibilityOffIcon /> : <VisibilityIcon />}
                </InputAdornment>
              ),
            }}
          />
          <Button
            variant="contained"
            sx={{ display: "block", mt: 2, mb: 8 }}
            size="large"
            type="submit"
            color="primary"
          >
            Подтвердить
          </Button>
          <Link to="/auth/login">
            <Typography variant="body2">
              У вас уже есть аккаунт? <span className="link yellow">Войти</span>
            </Typography>
          </Link>
          <Link to="/auth/registration">
            <Typography variant="body2" mt={2}>
              У вас ещё нету аккаунта?
              <span className="link yellow"> Зарегистрироваться</span>
            </Typography>
          </Link>
        </form>
      </>
    </AuthContainer>
  );
};

export default EnteringCode;
