import {
  Alert,
  Button,
  InputAdornment,
  Snackbar,
  TextField,
  Typography,
} from "@mui/material";
import { useFormik } from "formik";
import React, { useLayoutEffect, useRef, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link, useNavigate } from "react-router-dom";
import { CustomMask } from "../../../components/CustomMask";
import { sendCode, setCodeData } from "../../../redux/reducers/auth";
import AuthContainer from "../AuthContainer";
import CallIcon from "../CallIcon";

const Reset = ({ setLoading }) => {
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const [reset, setReset] = useState(false);

  const { code_data } = useSelector(({ auth }) => auth);

  const formik = useFormik({
    initialValues: {
      phone: "",
      type: 2,
    },
    onSubmit: (values) => {
      // show linear progress
      setLoading(true);

      // show message that code has been sent
      setReset(true);

      // remove some signs from phone number
      values.phone = values.phone.replace(/[\s+().,-]/g, "");

      // post data
      dispatch(sendCode(values));
    },
  });

  const firstUpdate = useRef(true);

  useLayoutEffect(() => {
    if (firstUpdate.current) {
      firstUpdate.current = false;
      return;
    }

    // hide linear progress
    setLoading(false);

    if (code_data.severity === 202) {
      setTimeout(() => {
        navigate("/auth/reset/verify");
        dispatch(setCodeData({ open: false, severity: 0 }));
      }, 1000);
    }
  }, [code_data]);

  return (
    <AuthContainer>
      <>
        <Snackbar
          open={code_data?.open}
          autoHideDuration={2000}
          anchorOrigin={{ vertical: "top", horizontal: "center" }}
        >
          <Alert
            severity={code_data?.severity === 202 ? "success" : "error"}
            variant="filled"
          >
            {code_data?.message && Object.values(code_data?.message)[0]}
          </Alert>
        </Snackbar>
        <form onSubmit={formik.handleSubmit}>
          <Typography variant="h6" mb={4}>
            Восстановить пароль
          </Typography>
          <Typography
            variant="body2"
            mb={4}
            sx={{
              lineHeight: "150%",
            }}
          >
            Введите номер телефона, на которую будет <br /> выслана инструкция
            по восстановлению пароля
          </Typography>
          <TextField
            variant="outlined"
            name="phone"
            inputMode="tel"
            id="+996 (000) 00 00 00"
            value={formik.values.phone}
            onChange={formik.handleChange}
            placeholder="Номер телефона"
            sx={{ mr: { xs: 0, lg: 4 }, width: { xs: "100%", lg: "auto" } }}
            InputProps={{
              inputProps: {
                inputMode: 'tel'
               },
              inputComponent: CustomMask,
              startAdornment: (
                <InputAdornment position="start">
                  <CallIcon />
                </InputAdornment>
              ),
            }}
          />
          {reset && (
            <Typography
              variant="body2"
              className="yellow"
              sx={{
                marginTop: "15px",
                lineHeight: "16px",
              }}
            >
              СМС отправлен на номер телефона
            </Typography>
          )}
          <Button
            variant="contained"
            sx={{ display: "block", mt: 2, mb: 8 }}
            size="large"
            type="submit"
            color="primary"
          >
            Восстановить
          </Button>
          <Link to="/auth/login">
            <Typography variant="body2">
              У вас уже есть аккаунт? <span className="link yellow">Войти</span>
            </Typography>
          </Link>
          <Link to="/auth/registration">
            <Typography variant="body2" mt={2}>
              У вас ещё нету аккаунта?
              <span className="link yellow"> Зарегистрироваться</span>
            </Typography>
          </Link>
        </form>
      </>
    </AuthContainer>
  );
};

export default Reset;
