import React from "react";
import { Button, Dialog, DialogContent, Typography } from "@mui/material";
import { Box } from "@mui/system";
import cookie from "cookie_js";
import CloseIcon from "@mui/icons-material/Close";
import CheckIcon from "@mui/icons-material/Check";

const LogoutDialog = ({ open, setOpen }) => {
  const handleLogout = () => {
    cookie.removeSpecific("token", { path: "/" });
  };

  return (
    <Dialog open={open} scroll="body" onClose={() => setOpen(false)}>
      <DialogContent sx={{ m: { sm: 0, xs: "15px" }, p: "15px" }}>
        <Typography
          id="modal-modal-title"
          variant="h2"
          component="h2"
          sx={{ textAlign: "center" }}
        >
          Вы действительно хотите выйти?
        </Typography>
        <Box sx={{ display: "flex", justifyContent: "center", mt: "25px" }}>
          <Button
            variant="contained"
            color="error"
            endIcon={<CloseIcon />}
            sx={{ mr: "25px" }}
            onClick={handleLogout}
          >
            Да
          </Button>
          <Button
            variant="contained"
            endIcon={<CheckIcon />}
            onClick={() => setOpen(false)}
          >
            Нет
          </Button>
        </Box>
      </DialogContent>
    </Dialog>
  );
};

export default LogoutDialog;
