import { Ordering } from "../../api";

const ORDERING_DATA = "ORDERING_DATA";
const GET_ORDER_INFO_DATA = "GET_ORDER_INFO_DATA";
const GET_ORDER_HISTORY_DATA = "GET_ORDER_HISTORY_DATA";
const GET_STATUSES_DATA = "GET_STATUSES_DATA";
const GET_BONUS_DATA = "GET_BONUS_DATA";

let initialState = {
  route: {},
  options: {},
  payment_data: {},
  orderHistory: {},
  orderInfo: "",
  delivery_types: {},
  packaging: {},
  order_data: {},
  envelops_prices: {},
  details: {},
  statuses: {},
  payment_types: {},
  detailsID: null,
  orderBonus: "",
};

const ordering = (state = initialState, action) => {
  switch (action.type) {
    case ORDERING_DATA: {
      return {
        ...state,
        route: action.data,
      };
    }

    case GET_STATUSES_DATA: {
      return {
        ...state,
        statuses: action.data,
      };
    }

    case "SET_DELIVERY_TYPES": {
      return {
        ...state,
        delivery_types: action.data,
      };
    }

    case GET_ORDER_HISTORY_DATA: {
      return {
        ...state,
        orderHistory: action.data,
      };
    }

    case GET_ORDER_INFO_DATA: {
      return {
        ...state,
        orderInfo: action.data,
      };
    }

    case GET_BONUS_DATA: {
      return {
        ...state,
        orderBonus: action.data,
      };
    }

    case "SET_OPTIONS": {
      return {
        ...state,
        options: action.data,
      };
    }

    case "SET_PAYMENT_DATA": {
      return {
        ...state,
        payment_data: action.data,
      };
    }

    case "SET_PACKAGING": {
      return {
        ...state,
        packaging: action.data,
      };
    }

    case "SET_ORDER_DATA": {
      return {
        ...state,
        order_data: action.data,
      };
    }

    case "SET_ENVELOPES_PRICES": {
      return {
        ...state,
        envelops_prices: action.data.sort(
          (a, b) => a.dimension.weight - b.dimension.weight
        ),
      };
    }

    case "SET_DETAILS": {
      return {
        ...state,
        details: action.data,
      };
    }

    case "SET_PAYMENT_TYPES": {
      return {
        ...state,
        payment_types: action.data,
      };
    }

    case "SET_DETAILS_ID": {
      return {
        ...state,
        detailsID: action.data,
      };
    }

    default:
      return state;
  }
};

export const setOrderingData = (data) => ({ type: ORDERING_DATA, data });
export const setStatusesData = (data) => ({ type: GET_STATUSES_DATA, data });

export const setDetailsID = (data) => ({ type: "SET_DETAILS_ID", data });
export const setOptions = (data) => ({ type: "SET_OPTIONS", data });

export const setDeliveryTypes = (data) => ({
  type: "SET_DELIVERY_TYPES",
  data,
});
export const setDetails = (data) => ({
  type: "SET_DETAILS",
  data,
});

export const setPaymentData = (data) => ({
  type: "SET_PAYMENT_DATA",
  data,
});

export const setPackaging = (data) => ({ type: "SET_PACKAGING", data });
export const setOrderData = (data) => ({ type: "SET_ORDER_DATA", data });
export const setEnvelopesPrices = (data) => ({
  type: "SET_ENVELOPES_PRICES",
  data,
});

export const seyPaymentTypes = (data) => ({
  type: "SET_PAYMENT_TYPES",
  data,
});

export const setOrderHistoryData = (data) => ({
  type: GET_ORDER_HISTORY_DATA,
  data,
});

export const setOrderInfoData = (data) => ({ type: GET_ORDER_INFO_DATA, data });
export const setBonusData = (data) => ({ type: GET_BONUS_DATA, data });

export const getOptions = () => {
  return (dispatch) => {
    Ordering.getOptions().then((response) => dispatch(setOptions(response)));
  };
};

export const getDeliveryTypes = () => {
  return (dispatch) => {
    Ordering.getDeliveryTypes().then((response) =>
      dispatch(setDeliveryTypes(response))
    );
  };
};

export const getPackaging = () => {
  return (dispatch) => {
    Ordering.getPackaging().then((response) =>
      dispatch(setPackaging(response))
    );
  };
};

export const makeOrder = (data) => {
  return (dispatch) => {
    Ordering.makeOrder(data).then((response) =>
      dispatch(
        setOrderData({
          open: true,
          data: response.data,
          severity: response.status,
        })
      )
    );
  };
};

export const addPhoto = (data) => () => {
  Ordering.addPhoto(data);
};

export const paymentBonus = (data) => {
  return (dispatch) => {
    Ordering.paymentBonus(data).then((response) =>
      dispatch(
        setPaymentData({
          open: true,
          data: response.data,
          severity: response.status,
        })
      )
    );
  };
};

export const getEnvelopesPrices = () => {
  return (dispatch) => {
    Ordering.getEnvelopesPrices().then((response) =>
      dispatch(setEnvelopesPrices(response))
    );
  };
};

export const getEnvelopesByFilter = (from, to) => {
  return (dispatch) => {
    Ordering.getEnvelopesByFilter(from, to).then((response) =>
      dispatch(setEnvelopesPrices(response))
    );
  };
};

export const getParcelDetails = (id) => {
  return (dispatch) => {
    Ordering.getParcelDetails(id).then((response) =>
      dispatch(setDetails(response))
    );
  };
};

export const getPaymentTypes = () => {
  return (dispatch) => {
    Ordering.getPaymentTypes().then((response) =>
      dispatch(seyPaymentTypes(response))
    );
  };
};

export const getOrderHistoryData = () => {
  return (dispatch) => {
    Ordering.getOrderHistoryData().then((response) =>
      dispatch(setOrderHistoryData(response))
    );
  };
};

export const getOrderInfoData = (id) => {
  return (dispatch) => {
    Ordering.getOrderInfoData(id).then((response) =>
      dispatch(setOrderInfoData(response))
    );
  };
};

export const getStatusesData = (id) => {
  return (dispatch) => {
    Ordering.getStatusesData(id).then((response) =>
      dispatch(setStatusesData(response))
    );
  };
};

export const getOrderBonusData = () => {
  return (dispatch) => {
    Ordering.getBonusData().then((response) =>
      dispatch(setBonusData(response))
    );
  };
};

export default ordering;
