import { Site } from "../../api";

const NEWS_DATA = "NEWS_DATA";
const ARTICLE_DATA = "ARTICLE_DATA";
const TARIFFS_DATA = "TARIFFS_DATA";
const SET_PARTNERS = "SET_PARTNERS";
const SET_QUESTIONS = "SET_QUESTIONS";

let initialState = {
  news: {},
  article: {},
  tariffs: {},
  partners: [],
  questions: [],
  feedback: "",
};

const site = (state = initialState, action) => {
  switch (action.type) {
    case NEWS_DATA: {
      return {
        ...state,
        news: action.data,
      };
    }

    case ARTICLE_DATA: {
      return {
        ...state,
        article: action.data,
      };
    }

    case TARIFFS_DATA: {
      return {
        ...state,
        tariffs: action.data,
      };
    }

    case SET_PARTNERS: {
      return {
        ...state,
        partners: action.data,
      };
    }

    case SET_QUESTIONS: {
      return {
        ...state,
        questions: action.data,
      };
    }

    case "SET_FEEDBACK_DATA": {
      return {
        ...state,
        feedback: action.data,
      };
    }

    default:
      return state;
  }
};

export const setNewsData = (data) => ({ type: NEWS_DATA, data });
export const setArticleData = (data) => ({ type: ARTICLE_DATA, data });
export const setTariffsData = (data) => ({ type: TARIFFS_DATA, data });
export const setPartners = (data) => ({ type: SET_PARTNERS, data });
export const setQuestions = (data) => ({ type: SET_QUESTIONS, data });
export const setFeedBackData = (data) => ({ type: "SET_FEEDBACK_DATA", data });

export const getNewsData = () => {
  return (dispatch) => {
    Site.getNewsData().then((response) => dispatch(setNewsData(response)));
  };
};

export const getArticleData = (id) => {
  return (dispatch) => {
    Site.getArticleData(id).then((response) =>
      dispatch(setArticleData(response))
    );
  };
};

export const getTariffsData = () => {
  return (dispatch) => {
    Site.getTariffsData().then((response) =>
      dispatch(setTariffsData(response))
    );
  };
};

export const getPartners = () => {
  return (dispatch) => {
    Site.getPartners().then((response) => dispatch(setPartners(response)));
  };
};

export const getQuestions = () => {
  return (dispatch) => {
    Site.getQuestions().then((response) => dispatch(setQuestions(response)));
  };
};

export const postFeedBackData = (data) => {
  return (dispatch) => {
    Site.postFeedBackData(data).then((response) =>
      dispatch(setFeedBackData(response))
    );
  };
};

export default site;
