/* eslint-disable eqeqeq */
import React, {
  lazy,
  useEffect,
  useLayoutEffect,
  useRef,
  useState,
} from "react";
import { Route, Navigate, Routes, useLocation } from "react-router";
import { Box } from "@mui/system";
import AdminSidebar from "../../shared/AdminSidebar";
import { Grid, LinearProgress } from "@mui/material";
import LogoutDialog from "../../shared/LogoutDialog";
import { useDispatch, useSelector } from "react-redux";
import { useFormik } from "formik";
import {
  addPhoto,
  getEnvelopesByFilter,
  makeOrder,
} from "../../redux/reducers/ordering";

const Ordering = lazy(() => import("../../pages/Dashboard/Ordering"));
const OrderInfo = lazy(() => import("../../pages/Dashboard/OrderInfo"));
const Contacts = lazy(() => import("../../pages/Contacts"));
const Bonus = lazy(() => import("../../pages/Dashboard/Bonus"));
const OrderHistory = lazy(() => import("../../pages/Dashboard/OrderHistory"));
const Profile = lazy(() => import("../../pages/Dashboard/Profile"));
const Change = lazy(() => import("../../pages/Dashboard/Profile/Change"));
const Tracking = lazy(() => import("../../pages/Dashboard/Tracking"));
const Calculator = lazy(() => import("../../pages/Dashboard/Calculator"));
const AdminHome = lazy(() => import("../../pages/Dashboard/AdminHome"));

const Dashboard = () => {
  const location = useLocation();
  const dispatch = useDispatch();

  const { envelops_prices, delivery_types, route, packaging, order_data } =
    useSelector(({ ordering }) => ordering);

  const [open, setOpen] = useState(false);
  const [step, setStep] = useState(1);
  const [files, setFiles] = useState([]);
  const [loading, setLoading] = useState(false);
  const [orderValues, setOrderValues] = useState({});
  const firstUpdate = useRef(true);

  const handleOpen = () => setOpen(true);

  const formik = useFormik({
    initialValues: {
      geoCodeFrom: "",
      geoCodeTo: "",
      from: route?.from ? route?.from : "",
      to: route?.to ? route?.to : "",
      districtTo: "",
      villageTo: "",
      payment: "",
      delivery_type: route?.delivery_type ? route?.delivery_type : "",
      envelop: route?.envelop ? route?.envelop : "",
      packaging: route?.packaging ? route?.packaging : "",
      senderPhone: "",
      senderInfo: "",
      senderCompany: "",
      senderEmail: "",
      recipientPhone: "",
      recipientInfo: "",
      recipientCompany: "",
      recipientEmail: "",
      phone: "",
      info: "",
      type: "",
      amount: null,
      title: "",
      description: "",
      sending_date: "",
      status: "",
      paymentConfirmation: [1],
      option: [6],
      weight: route?.weight ? route?.weight : "",
      district: "",
      width: route?.width ? route?.width : "",
      height: route?.height ? route?.height : "",
      length: route?.length ? route?.length : "",
      price: 0,
      sum: 0,
    },

    onSubmit: (values) => {
      if (location.pathname === "/dashboard/ordering") {
        if (values && step < 8) setStep(step + 1);

        if (step > 6) {
          // find selected envelop for calculate price
          const selectedEnvelop = envelops_prices.find(
            (item) => item.title == formik.values.envelop
          );

          // find selected delivery type for calculate price
          const selectedDeliveryType = delivery_types.find(
            (item) => item.title == formik.values.delivery_type
          );

          // find selected packaging  for calculate price
          const selectedPackaging = packaging.find(
            (item) => item.title == formik.values.packaging
          );

          setOrderValues({
            envelop: selectedEnvelop?.id,
            packaging: selectedPackaging.id,
            delivery_type: selectedDeliveryType.id,
          });

          if (
            selectedEnvelop &&
            formik.values.weight &&
            formik.values.height &&
            formik.values.length &&
            formik.values.width &&
            formik.values.amountForSize
          ) {
            formik.setFieldValue(
              "price",
              parseInt(formik.values.amountForSize) +
                parseInt(selectedEnvelop?.price) +
                parseInt(selectedDeliveryType?.price) +
                parseInt(selectedPackaging?.price)
            );
          } else if (selectedEnvelop) {
            formik.setFieldValue("dimension", selectedEnvelop?.dimension);
            formik.setFieldValue(
              "price",
              parseInt(selectedEnvelop?.price) +
                parseInt(selectedDeliveryType?.price) +
                parseInt(selectedPackaging?.price)
            );
          } else {
            formik.setFieldValue(
              "price",
              parseInt(formik.values.amountForSize) +
                parseInt(selectedDeliveryType.price) +
                parseInt(selectedPackaging.price)
            );
          }
        }

        const data = {
          payment: {
            envelop: orderValues.envelop,
            delivery_type: orderValues.delivery_type,
            packaging: [orderValues.packaging],
          },

          direction: [
            {
              type: 1,
              description: values.fromDescr,
              destination: values.destinationFrom,
              geolocation: values?.geo?.toString(),
              district: values.from,
            },
            {
              type: 2,
              description: values.toDescr,
              district: values.to,
              destination: values.destinationTo,
            },
          ],

          user_info: [
            {
              phone: values.senderPhone?.replace(/[\s+().,-]/g, ""),
              info: values.senderInfo,
              company: values.senderCompany,
              email: values.senderEmail,
              type: 1,
            },
            {
              phone: values.recipientPhone?.replace(/[\s+().,-]/g, ""),
              info: values.recipientInfo,
              company: values.recipientCompany,
              email: values.recipientEmail,
              type: 2,
            },
          ],

          dimension: values.dimension,
          sending_date: values.date,
          option: values.option,
          title: "A",
        };

        if (step == 8) {
          setLoading(true);
          dispatch(makeOrder(data));
        }
      } else {
        const selectedEnvelop = envelops_prices.find(
          (item) => item.title == formik.values.envelop
        );

        // find selected delivery type for calculate price
        const selectedDeliveryType = delivery_types.find(
          (item) => item.title == formik.values.delivery_type
        );

        // find selected packaging  for calculate price
        const selectedPackaging = packaging.find(
          (item) => item.title == formik.values.packaging
        );

        if (
          selectedEnvelop &&
          formik.values.weight &&
          formik.values.height &&
          formik.values.length &&
          formik.values.width &&
          formik.values.amountForSize
        ) {
          formik.setFieldValue(
            "price",
            parseInt(formik.values.amountForSize) +
              parseInt(selectedEnvelop?.price) +
              parseInt(selectedDeliveryType?.price) +
              parseInt(selectedPackaging?.price)
          );
        } else if (selectedEnvelop) {
          formik.setFieldValue(
            "price",
            parseInt(selectedEnvelop?.price) +
              parseInt(selectedDeliveryType?.price) +
              parseInt(selectedPackaging?.price)
          );
        } else {
          formik.setFieldValue(
            "price",
            parseInt(formik.values.amountForSize) +
              parseInt(selectedDeliveryType.price) +
              parseInt(selectedPackaging.price)
          );
        }
      }
    },
  });

  useEffect(() => {
    if (Array.isArray(envelops_prices)) {
      if (
        formik.values.weight &&
        formik.values.height &&
        formik.values.length &&
        formik.values.width
      ) {
        formik.setFieldValue("envelop", null);

        const correctDimension = envelops_prices?.find(
          (item) =>
            item?.dimension?.length >= formik?.values?.length &&
            item?.dimension?.height >= formik?.values?.height &&
            item?.dimension?.width >= formik?.values?.width
        );

        if (correctDimension?.dimension?.weight < formik?.values?.weight) {
          formik.setFieldValue(
            "amountForSize",
            correctDimension?.kilo *
              (formik.values.weight - correctDimension?.dimension.weight)
          );
        }

        if (correctDimension) {
          formik.setFieldValue("envelop", correctDimension?.title);
          formik.setFieldValue("dimension", correctDimension?.dimension);
        } else {
          const correctDimension = envelops_prices
            ?.sort((a, b) => b.dimension.weight - a.dimension.weight)
            .find(
              (item) =>
                item?.dimension?.length <= formik?.values?.length &&
                item?.dimension?.height <= formik?.values?.height &&
                item?.dimension?.width <= formik?.values?.width
            );
          formik.setFieldValue(
            "amountForSize",
            ((formik.values.length *
              formik.values.width *
              formik.values.height) /
              1000000) *
              1000 +
              correctDimension?.kilo *
                (formik.values.weight - correctDimension?.dimension?.weight) +
              parseInt(correctDimension?.price)
          );
          formik.setFieldValue("dimension", {
            length: formik.values.length,
            width: formik.values.width,
            height: formik.values.height,
            weight: formik.values.weight,
          });
        }
      }
    }

    if (
      !formik.values.weight ||
      !formik.values.height ||
      !formik.values.length ||
      !formik.values.width
    ) {
      formik.setFieldValue("envelop_label", null);
    }
    // eslint-disable-next-line
  }, [
    formik.values.envelop,
    formik.values.weight,
    formik.values.height,
    formik.values.length,
    formik.values.width,
  ]);

  useEffect(() => {
    if (formik.values.from && formik.values.to) {
      dispatch(getEnvelopesByFilter(formik.values.from, formik.values.to));
    }
    // eslint-disable-next-line
  }, [formik.values.from, formik.values.to]);

  useLayoutEffect(() => {
    if (firstUpdate.current) {
      firstUpdate.current = false;
      return;
    }
    setLoading(false);

    if (order_data.severity === 201) {
      setStep(9);
      if (files.length >= 1) {
        const data = new FormData();
        files.map((item) => data.append("img", item));
        data.append("parcel", order_data?.data?.id);
        dispatch(addPhoto(data));
      }
    }
  }, [order_data]);

  useEffect(() => {
    if (location.pathname !== "/dashboard/ordering") {
      setStep(1);
      formik.resetForm();
    }
    if (location.pathname === "/dashboard/home") {
      document.body.classList.add("tidioShow");
    }
    // eslint-disable-next-line
  }, [location.pathname]);

  return (
    <Grid container>
      {loading && (
        <LinearProgress
          color="primary"
          sx={{
            position: "fixed",
            zIndex: 1201,
            top: { xs: 0, xl: 0 },
            left: 0,
            width: "100%",
          }}
        />
      )}
      <AdminSidebar handleOpen={handleOpen} />
      <LogoutDialog open={open} setOpen={setOpen} />
      <Box
        component="main"
        sx={{
          flexGrow: 1,
          width: { xl: `calc(100% - 300px)` },
          bgcolor: "#F6F7F9",
          minHeight: "100vh",
          padding:
            location.pathname === "/dashboard/home"
              ? ""
              : {
                  xs: "40px 16px",
                  lg: "80px 32px",
                },
        }}
      >
        <Routes>
          <Route path="/" element={<Navigate to="/dashboard/home" />} />
          <Route path="/home" element={<AdminHome formik={formik} />} />
          <Route path="/order-info/:id" element={<OrderInfo />} />
          <Route path="/order-history" element={<OrderHistory />} />
          <Route path="/bonus" element={<Bonus />} />
          <Route path="/contacts" element={<Contacts admin />} />
          <Route
            path="/profile"
            element={<Profile setLoading={setLoading} />}
          />
          <Route
            path="/profile/edit"
            element={<Change setLoading={setLoading} />}
          />
          <Route
            path="/tracking"
            element={<Tracking setLoading={setLoading} />}
          />
          {formik.values.from && formik.values.to ? (
            <Route
              path="/ordering"
              element={
                <Ordering
                  step={step}
                  setStep={setStep}
                  loading={loading}
                  formik={formik}
                  setFiles={setFiles}
                  files={files}
                />
              }
            />
          ) : (
            <Route
              path="/ordering"
              element={<Navigate to="/dashboard/home" />}
            />
          )}

          <Route path="/calculator" element={<Calculator formik={formik} />} />
        </Routes>
      </Box>
    </Grid>
  );
};

export default Dashboard;
