import React, { useEffect, useState } from "react";
import {
  Alert,
  Button,
  Checkbox,
  FormControlLabel,
  InputAdornment,
  Snackbar,
  TextField,
  Typography,
} from "@mui/material";
import { Box } from "@mui/system";
import { useFormik } from "formik";
import * as Yup from "yup";
import { Link, useNavigate } from "react-router-dom";
import AuthContainer from "../AuthContainer";
import CallIcon from "../CallIcon";
import LockIcon from "../LockIcon";
import UserIcon from "../UserIcon";
import VisibilityOffIcon from "@mui/icons-material/VisibilityOff";
import VisibilityIcon from "@mui/icons-material/Visibility";
import {
  sendCode,
  sendRegData,
  setRegData,
} from "../../../redux/reducers/auth";
import { useDispatch, useSelector } from "react-redux";
import cookie from "cookie_js";
import { CustomMask } from "../../../components/CustomMask";

const Registration = ({ setLoading }) => {
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const [visible, setVisible] = useState(false);

  const { registration } = useSelector(({ auth }) => auth);

  const validationSearch = Yup.object().shape({
    passwordConfirmation: Yup.string().oneOf(
      [Yup.ref("password"), null],
      "Пароли должны совпадать"
    ),
  });

  const formik = useFormik({
    initialValues: {
      phone: "",
      password: "",
      passwordConfirmation: "",
      info: "",
      patronymic: "MEN",
      region: "",
      city: "",
    },
    validationSchema: validationSearch,
    onSubmit: (values) => {
      // show linear progress
      setLoading(true);

      //remove different characters from phone
      values.phone = values.phone.replace(/[\s+().,-]/g, "");
      // send reg data
      dispatch(sendRegData(values));

      // set to cookie user data
      cookie.set("password", values.password);
      cookie.set("phone", values.phone);
    },
  });

  useEffect(() => {
    // hide linear progress
    setLoading(false);

    if (registration.severity === 201) {
      // If answer is successful send code to phone
      dispatch(
        sendCode({
          type: 1,
          phone: cookie.get("phone"),
        })
      );

      // after 1 second navigate to dashboard
      setTimeout(() => {
        dispatch(
          setRegData({
            ...registration,
            open: false,
            severity: null,
          })
        );
        navigate("/auth/activation");
        dispatch(setRegData({ open: false, severity: 0 }));
      }, 1000);
    }
  }, [registration]);

  return (
    <AuthContainer>
      <>
        <Snackbar
          open={registration.open}
          autoHideDuration={2000}
          anchorOrigin={{ vertical: "top", horizontal: "center" }}
        >
          <Alert
            severity={registration.severity === 201 ? "success" : "error"}
            variant="filled"
          >
            {registration.severity === 201
              ? "Регистрация успешно пройдена"
              : registration?.message &&
                Object.values(registration?.message)[0]}
          </Alert>
        </Snackbar>
        <form onSubmit={formik.handleSubmit}>
          <Typography variant="h6" mb={4}>
            Регистрация
          </Typography>
          <Box
            display="flex"
            width="100%"
            sx={{
              flexDirection: { xs: "column", md: "row" },
            }}
          >
            <TextField
              required
              fullWidth
              name="phone"
              autoComplete="username"
              id="+996 (000) 00 00 00"
              value={formik.values.phone}
              onChange={formik.handleChange}
              variant="outlined"
              placeholder="Номер телефона"
              sx={{ mr: { xs: 4, md: 4 }, mb: "16px" }}
              InputProps={{
                inputComponent: CustomMask,
                inputProps: {
                  inputMode: 'tel'
                 },
                startAdornment: (
                  <InputAdornment position="start">
                    <CallIcon />
                  </InputAdornment>
                ),
              }}
            />

            <TextField
              required
              fullWidth
              name="info"
              type="text"
              autoComplete="name"
              value={formik.values.info}
              onChange={formik.handleChange}
              variant="outlined"
              placeholder="ФИО"
              sx={{ mb: "16px" }}
              InputProps={{
                startAdornment: (
                  <InputAdornment position="start">
                    <UserIcon />
                  </InputAdornment>
                ),
              }}
            />
          </Box>
          <Box
            display="flex"
            width="100"
            sx={{ flexDirection: { xs: "column", md: "row" } }}
          >
            <TextField
              required
              fullWidth
              name="password"
              autoComplete="current-password"
              value={formik.values.password}
              onChange={formik.handleChange}
              type={visible ? "text" : "password"}
              variant="outlined"
              helperText="Минимум 8 символов"
              placeholder="Пароль"
              sx={{ mr: { xs: 4, lg: 4 }, mb: "16px" }}
              InputProps={{
                startAdornment: (
                  <InputAdornment position="start">
                    <LockIcon />
                  </InputAdornment>
                ),
                endAdornment: (
                  <InputAdornment
                    position="end"
                    onClick={() => setVisible(!visible)}
                  >
                    {!visible ? <VisibilityOffIcon /> : <VisibilityIcon />}
                  </InputAdornment>
                ),

                inputProps: {
                  pattern: ".{6,}",
                },
              }}
            />

            <TextField
              required
              fullWidth
              autoComplete="current-password"
              name="passwordConfirmation"
              value={formik.values.passwordConfirmation}
              type={visible ? "text" : "password"}
              error={formik.errors.passwordConfirmation}
              onChange={formik.handleChange}
              helperText={formik.errors.passwordConfirmation}
              variant="outlined"
              placeholder="Повторите пароль"
              sx={{ mr: { xs: 0, lg: 0 }, mb: "16px" }}
              InputProps={{
                startAdornment: (
                  <InputAdornment position="start">
                    <LockIcon />
                  </InputAdornment>
                ),
                endAdornment: (
                  <InputAdornment
                    position="end"
                    onClick={() => setVisible(!visible)}
                  >
                    {!visible ? <VisibilityOffIcon /> : <VisibilityIcon />}
                  </InputAdornment>
                ),
              }}
            />
          </Box>
          <Box component="label" sx={{ mt: { xs: 0.5, lg: 5 } }}>
            <Checkbox defaultChecked required color="primary" />
            <Typography
              variant="h6"
              component="span"
              sx={{
                fontStyle: "normal",
                fontWeight: "normal",
                fontSize: { xs: 14, lg: 16 },
                lineHeight: "150%",
                color: "#000000",
              }}
            >
              Нажимая на кнопку «Зарегистрироваться», Вы принимаете условия{" "}
              <Link
                to="/user-agreement"
                target="_blank"
                style={{
                  textDecoration: "underline",
                }}
              >
                Пользовательского соглашения
              </Link>
            </Typography>
          </Box>
          <Button
            variant="contained"
            type="submit"
            sx={{ display: "block", mt: { xs: 2, lg: 5 }, mb: 8 }}
            size="large"
            color="primary"
          >
            Зарегистрироваться
          </Button>
          <Link to="/auth/login">
            <Typography variant="body2">
              У вас уже есть аккаунт? <span className="link yellow">Войти</span>
            </Typography>
          </Link>
          <Link to="/auth/reset">
            <Typography variant="body2" mt={2}>
              Забыли пароль? <span className="link yellow">Восстановить</span>
            </Typography>
          </Link>
        </form>
      </>
    </AuthContainer>
  );
};

export default Registration;
