let initialState = {
  isDrawerOpen: false,
  feedbackOpen: false,
  termsOfUseOpen: false,
};

const main = (state = initialState, action) => {
  switch (action.type) {
    case "DRAWER_OPEN": {
      return {
        ...state,
        isDrawerOpen: action.data,
      };
    }

    case "SET_FEEDBACK_DIALOG": {
      return {
        ...state,
        feedbackOpen: action.data,
      };
    }

    case "SET_TERMS_DIALOG": {
      return {
        ...state,
        termsOfUseOpen: action.data,
      };
    }

    default:
      return state;
  }
};

export const setDrawerOpen = (data) => ({ type: "DRAWER_OPEN", data });
export const setFeedbackDialog = (data) => ({
  type: "SET_FEEDBACK_DIALOG",
  data,
});

export const setTermsOfUseDialog = (data) => ({
  type: "SET_TERMS_DIALOG",
  data,
});

export default main;
