import { createTheme } from "@mui/material";
import { createBreakpoints, ThemeProvider } from "@mui/system";
import { useEffect } from "react";
import { Route, Routes, useLocation } from "react-router";
import "remixicon/fonts/remixicon.css";
import "./css/global.css";
import Auth from "./Routers/Auth";
import Dashboard from "./Routers/Dashboard";
import Site from "./Routers/Site";
import SiteDrawer from "./shared/Drawer";
import FeedBack from "./shared/FeedBack";
import { YMaps } from "react-yandex-maps";
import { useDispatch, useSelector } from "react-redux";
import { getAccountData, getRegionsData } from "./redux/reducers/account";
import ProtectedRoutes, { useAuth } from "./shared/ProtectedRoutes";
import { setDrawerOpen } from "./redux/reducers/main";
import TermsOfUse from "./shared/TermsOfUse";
import { getEnvelopesPrices } from "./redux/reducers/ordering";
import cookie from "cookie_js";

function App() {
  const isAuth = useAuth();
  const location = useLocation();
  const dispatch = useDispatch();
  const { isDrawerOpen } = useSelector(({ main }) => main);
  const { account } = useSelector(({ account }) => account);

  useEffect(() => {
    window.scrollTo(0, 0);
    dispatch(getRegionsData());
    dispatch(getEnvelopesPrices());

    if (isAuth) {
      dispatch(getAccountData());
    }

    if (account?.status === 401) {
      cookie.removeSpecific("token", { path: "/" });
      cookie.remove("token");
    }
    // eslint-disable-next-line
  }, []);

  useEffect(() => {
    window.scrollTo(0, 0);
    if (isDrawerOpen) dispatch(setDrawerOpen(false));
    // eslint-disable-next-line
  }, [location.pathname]);

  return (
    <ThemeProvider theme={theme}>
      <YMaps
        query={{
          ns: "ymaps", // name space
          // apikey: "a8b0be0e-c7f1-4511-896f-3feef05fffba" // doce.kg
          apikey: "de0d2bc3-0c63-4df5-b627-62a6dae7bdf8",
        }}
      >
        <Routes>
          <Route path="/*" element={<Site />} />
          <Route element={<ProtectedRoutes />}>
            <Route path="/dashboard/*" element={<Dashboard />} />
          </Route>
          <Route path="/auth/*" element={<Auth />} />
        </Routes>
      </YMaps>
      <FeedBack />
      <TermsOfUse />
      <SiteDrawer />
    </ThemeProvider>
  );
}

export default App;

