import { Alert, Button, Snackbar, TextField, Typography } from "@mui/material";
import cookie from "cookie_js";
import { useFormik } from "formik";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router";
import { Link } from "react-router-dom";
import { instance } from "../../../api";
import { getAccountData } from "../../../redux/reducers/account";
import {
  postLoginData,
  sendCode,
  sendCodeToVerify,
  setAnswerVerify,
} from "../../../redux/reducers/auth";
import AuthContainer from "../AuthContainer";

const Activation = ({ setLoading }) => {
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const [count, setCount] = useState(30);
  const [sended, setSended] = useState(false);

  const { answer_verify, login } = useSelector(({ auth }) => auth);

  const formik = useFormik({
    initialValues: {
      code: "",
    },

    onSubmit: (values) => {
      //show linear progress
      setLoading(true);

      // post code
      dispatch(sendCodeToVerify(values));
    },
  });

  useEffect(() => {
    // hide linear progress
    setLoading(false);

    if (answer_verify.severity === 202) {
      dispatch(
        postLoginData({
          password: cookie.get("password"),
          phone: cookie.get("phone"),
        })
      );

      if (login.message.access) {
        instance.defaults.headers.Authorization = `Bearer ${login?.message?.access}`;
        cookie.set("token", login?.message?.access);
      }

      setTimeout(() => {
        navigate("/dashboard/home");
        dispatch(getAccountData());
        dispatch(setAnswerVerify({ open: false, severity: 0 }));
      }, 2000);
    }
  }, [answer_verify]);

  useEffect(() => {
    if (count > 0)
      setTimeout(() => {
        setCount(count - 1);
      }, 1000);
  });

  const submitHandler = () => {
    if (count === 0) {
      dispatch(
        sendCode({
          type: 1,
          phone: cookie.get("phone"),
        })
      );
      setSended(true);
      setCount(30);
    }
  };

  return (
    <AuthContainer>
      <>
        <Snackbar
          open={answer_verify.open}
          autoHideDuration={2000}
          anchorOrigin={{ vertical: "top", horizontal: "center" }}
          onClose={() =>
            dispatch(
              setAnswerVerify({
                ...answer_verify,
                open: false,
              })
            )
          }
        >
          <Alert
            severity={answer_verify.severity === 202 ? "success" : "error"}
            variant="filled"
          >
            {answer_verify.severity === 200
              ? "Успешний вход в систему"
              : answer_verify?.message?.message}
          </Alert>
        </Snackbar>
        <Snackbar
          open={sended}
          autoHideDuration={2000}
          anchorOrigin={{ vertical: "top", horizontal: "center" }}
          onClose={() => setSended(false)}
        >
          <Alert severity={"success"} variant="filled">
            Успешно отправлено
          </Alert>
        </Snackbar>
        <form onSubmit={formik.handleSubmit}>
          <Typography variant="h6" mb={4}>
            Активация аккаунта
          </Typography>
          <TextField
            variant="outlined"
            name="code"
            value={formik.values.code}
            onChange={formik.handleChange}
            placeholder="Введите код"
            sx={{ width: { xs: "100%", lg: "270px" } }}
          />
          <Typography
            variant="body2"
            className="yellow"
            sx={{
              marginTop: "15px",
              lineHeight: "16px",
            }}
          >
            СМС отправлен на номер телефона
          </Typography>
          <Typography variant="body2" mt={2} onClick={submitHandler}>
            Не пришло сообщение? Можно{" "}
            <span className={count == 0 ? "link yellow" : "link disabled"}>
              прислать ещё
            </span>{" "}
            через {count} секунд
          </Typography>

          <Button
            variant="contained"
            type="submit"
            sx={{ display: "block", mt: { xs: 2, lg: 5 }, mb: 8 }}
            size="large"
            color="primary"
          >
            Зарегистрироваться
          </Button>

          <Link to="/auth/login">
            <Typography variant="body2">
              У вас уже есть аккаунт?{" "}
              <span className="link yellow ">Войти</span>
            </Typography>
          </Link>
          <Link to="/auth/reset">
            <Typography variant="body2" mt={2}>
              Забыли пароль? <span className="link yellow">Восстановить</span>
            </Typography>
          </Link>
        </form>
      </>
    </AuthContainer>
  );
};

export default Activation;
