import { LinearProgress } from "@mui/material";
import { makeStyles } from "@mui/styles";
import React, { lazy, useState } from "react";
import { Route, Routes } from "react-router";
import Activation from "../../pages/Auth/Activation";
import Registration from "../../pages/Auth/Registration";
import Reset from "../../pages/Auth/Reset";
import EnteringCode from "../../pages/Auth/Reset/EnteringCode";
import Header from "../../shared/Header";
const Login = lazy(() => import("../../pages/Auth/Login"));

const Auth = () => {
  const classes = useStyles();
  const [loading, setLoading] = useState(false);
  return (
    <>
      <Header />
      {loading && (
        <LinearProgress
          color="primary"
          sx={{
            position: "fixed",
            zIndex: 1000,
            top: { xs: 80, xl: 100 },
            left: 0,
            width: "100%",
          }}
        />
      )}
      <main className={classes.main}>
        <Routes>
          <Route
            path="/login"
            element={<Login loading={loading} setLoading={setLoading} />}
          />
          <Route
            path="/registration"
            element={<Registration loading={loading} setLoading={setLoading} />}
          />
          <Route
            path="/reset"
            element={<Reset loading={loading} setLoading={setLoading} />}
          />
          <Route
            path="/reset/verify"
            element={<EnteringCode loading={loading} setLoading={setLoading} />}
          />
          <Route
            path="/activation"
            element={<Activation loading={loading} setLoading={setLoading} />}
          />
        </Routes>
      </main>
    </>
  );
};

export default Auth;

const useStyles = makeStyles(() => ({
  main: {
    padding: "120px 0 200px",
  },
}));
