import { Avatar, Button, Typography } from "@mui/material";
import { Box } from "@mui/system";
import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { makeStyles } from "@mui/styles";
import { useLocation } from "react-router";
import Link from "@mui/material/Link";

export const BonusCard = () => {
  const dispatch = useDispatch();
  const { account } = useSelector(({ account }) => account);

  return (
    <Button
      component="a"
      size="unset"
      href="/dashboard/bonus"
      sx={{
        borderRadius: "10px",
        p: {xl:"15px 13px",xs:'10px'},
        width: "169px",
        height: "64px",
        display: "block",
        textTransform: "unset",
      }}
      className="btn-bg"
    >
      <Typography sx={{ fontWeight: "500", fontSize: "14px", color: "#000",mb:'5px' }}>
        Накопленные бонусы
      </Typography>
      <Typography
        sx={{ fontWeight: "700", fontSize: { xs: "20px" }, color: "#000" }}
      >
        {account && account.points} сом
      </Typography>
    </Button>
  );
};

export const UserAvatar = ({ account, classes }) => {
  return (
    <>
      {account?.avatar && (
        <Avatar
          alt="user"
          src={account.avatar}
          sx={{
            width: 50,
            height: 50,
            borderRadius: "8px",
            mr: "15px",
            border: "1px solid #F3F3F3",
          }}
        />
      )}
      {!account?.avatar && (
        <Avatar
          sx={{ bgcolor: "#FFB951", mr: "15px" }}
          className={classes.userAvatarStyles}
        >
          <i className="ri-user-3-fill" />
        </Avatar>
      )}
    </>
  );
};

export const UserName = ({ account }) => {
  return (
    <Typography
      variant="body2"
      sx={{ color: "#000", fontSize: { xs: "16px" }, m: "25px 27px 25px 0" }}
    >
      {account.info && account.info}
    </Typography>
  );
};

const User = () => {
  const { account } = useSelector(({ account }) => account);

  const classes = useStyles();
  if(!account) return "Загрузка..."
  return (
    <>
      <Link
        href="/dashboard/profile"
        sx={{ textDecoration: "none" }}
        title="Изменить профиль"
      >
        <Box sx={{ display: "flex", alignItems: "center" }}>
          <UserAvatar account={account} classes={classes} />
          <UserName account={account} />
        </Box>
      </Link>
      <BonusCard />
    </>
  );
};
export default User;

const useStyles = makeStyles(() => ({
  userAvatarStyles: {
    width: "50px",
    height: "50px",
    borderRadius: "11px",
    fontSize: "24px",
    transition: "all 0.3s ease-in-out",
    color: "#fff",
    
  },
}));
