import React from "react";
import CloseIcon from "@mui/icons-material/Close";
import CheckIcon from "@mui/icons-material/Check";
import cookie from "cookie_js";
import {
  AppBar,
  Button,
  Drawer,
  Grid,
  IconButton,
  List,
  ListItem,
  ListItemButton,
  ListItemIcon,
  ListItemText,
  Modal,
  Toolbar,
  Typography,
  useMediaQuery,
} from "@mui/material";
import { Box } from "@mui/system";
import logo from "../../assets/images/logo.png";
import { NavLink, useNavigate } from "react-router-dom";
import User, { BonusCard } from "./User";

const AdminSidebar = (props) => {
  const responsive = useMediaQuery("(max-width:1199px) and (min-width:0px)");
  const mobile = useMediaQuery("(max-width:575px) and (min-width:0px)");

  const [mobileOpen, setMobileOpen] = React.useState(false);
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  const handleDrawerClose = () => {
    setMobileOpen(!mobileOpen);
  };

  let navigate = useNavigate();

  const handleLogout = () => {
    handleClose();
    cookie.removeSpecific("token", { path: "/" });
    cookie.remove("token");
    setTimeout(() => {
      navigate("/");
      window.location.reload("/");
    }, 1000);
  };

  const drawer = (
    <Box
      sx={{
        p: {
          xxl: "60px 0 60px 15px",
          md: "30px 15px",
          xs: "50px 20px 50px 40px",
        },
      }}
    >
      <Box
        display="flex"
        sx={{ justifyContent: { xl: "center", xs: "space-between" } }}
      >
        <Button href="/" size="unset">
          <img src={logo} width="113px" alt="" />
        </Button>

        {responsive && (
          <IconButton onClick={handleDrawerClose}>
            <i
              className="ri-close-line"
              style={{
                background: "#C4C4C4",
                width: "43px",
                height: "43px",
                borderRadius: "50%",
                color: "#fff",
                display: "inline-flex",
                alignItems: "center",
                justifyContent: "center",
              }}
            />
          </IconButton>
        )}
      </Box>
      {responsive && (
        <Box
          sx={{
            mt: "30px",

            display: "flex",
          }}
        >
          <User />
        </Box>
      )}
      <List
        sx={{
          width: "100%",
          maxWidth: 360,
          marginBottom: "20px",
          p: { xs: "25px 0" },
          "& .MuiTypography-root": {
            fontSize: "16px",
            fontWeight: "600",
          },
        }}
        className="sidebarList"
      >
        <ListItem sx={{ p: { xxl: "12px 0", xs: "4.5px 0" } }}>
          <ListItemButton
            component={NavLink}
            to="/dashboard/home"
            onClick={handleDrawerToggle}
          >
            <ListItemIcon>
              <i className="ri-home-4-line" />
            </ListItemIcon>
            <ListItemText
              sx={{ m: { md: 0, xs: "7px 0" } }}
              primary="Заказать"
            />
          </ListItemButton>
        </ListItem>
        <ListItem sx={{ p: { xxl: "12px 0", xs: "4.5px 0" } }}>
          <ListItemButton
            component={NavLink}
            to="/dashboard/calculator"
            onClick={handleDrawerToggle}
          >
            <ListItemIcon>
              <i className="ri-calculator-line" />
            </ListItemIcon>
            <ListItemText
              sx={{ m: { md: 0, xs: "7px 0" } }}
              primary="Онлайн калькулятор"
            />
          </ListItemButton>
        </ListItem>
        <ListItem sx={{ p: { xxl: "12px 0", xs: "4.5px 0" } }}>
          <ListItemButton
            component={NavLink}
            to="/dashboard/tracking"
            onClick={handleDrawerToggle}
          >
            <ListItemIcon>
              <i className="ri-road-map-line" />
            </ListItemIcon>
            <ListItemText
              sx={{ m: { md: 0, xs: "7px 0" } }}
              primary="Отследить заказ"
            />
          </ListItemButton>
        </ListItem>
        <ListItem sx={{ p: { xxl: "12px 0", xs: "4.5px 0" } }}>
          <ListItemButton
            component={NavLink}
            to="/dashboard/bonus"
            onClick={handleDrawerToggle}
          >
            <ListItemIcon>
              <i className="ri-bank-card-2-line" />
            </ListItemIcon>
            <ListItemText
              sx={{ m: { md: 0, xs: "7px 0" } }}
              primary="Бонусная карта"
            />
          </ListItemButton>
        </ListItem>
        <ListItem sx={{ p: { xxl: "12px 0", xs: "4.5px 0" } }}>
          <ListItemButton
            component={NavLink}
            to="/dashboard/order-history"
            onClick={handleDrawerToggle}
          >
            <ListItemIcon>
              <i className="ri-newspaper-line" />
            </ListItemIcon>
            <ListItemText
              sx={{ m: { md: 0, xs: "7px 0" } }}
              primary="История заказов"
            />
          </ListItemButton>
        </ListItem>
        <ListItem sx={{ p: { xxl: "12px 0", xs: "4.5px 0" } }}>
          <ListItemButton
            component={NavLink}
            to="/dashboard/profile"
            onClick={handleDrawerToggle}
          >
            <ListItemIcon>
              <i className="ri-user-3-line" />
            </ListItemIcon>
            <ListItemText
              sx={{ m: { md: 0, xs: "7px 0" } }}
              primary="Мой профиль"
            />
          </ListItemButton>
        </ListItem>
        <ListItem sx={{ p: { xxl: "12px 0", xs: "4.5px 0" } }}>
          <ListItemButton
            component={NavLink}
            to="/dashboard/contacts"
            onClick={handleDrawerToggle}
          >
            <ListItemIcon>
              <i className="ri-phone-line" />
            </ListItemIcon>
            <ListItemText
              sx={{ m: { md: 0, xs: "7px 0" } }}
              primary="Контакты"
            />
          </ListItemButton>
        </ListItem>
        <ListItem sx={{ p: { xxl: "12px 0", xs: "4.5px 0" } }}>
          <ListItemButton
            component={NavLink}
            to="/"
            onClick={handleDrawerToggle}
          >
            <ListItemIcon>
              <i className="ri-global-line" />
            </ListItemIcon>
            <ListItemText
              sx={{ m: { md: 0, xs: "7px 0" } }}
              primary="Перейти на сайт"
            />
          </ListItemButton>
        </ListItem>
        <ListItem
          sx={{
            mt: { xs: "0" },
            p: { xxl: "12px 0", xs: "4.5px 0" },
          }}
        >
          <ListItemButton component="a" onClick={handleOpen}>
            <ListItemIcon>
              <i className="ri-walk-line" />
            </ListItemIcon>
            <ListItemText sx={{ m: { md: 0, xs: "7px 0" } }} primary="Выйти" />
          </ListItemButton>
        </ListItem>
      </List>
    </Box>
  );

  const container = document.body;

  return (
    <>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>
          <Box>
            <Typography
              id="modal-modal-title"
              variant="h2"
              component="h2"
              sx={{ textAlign: "center" }}
            >
              Вы действительно хотите выйти?
            </Typography>
            <Box sx={{ display: "flex", justifyContent: "center", mt: "25px" }}>
              <Button
                variant="contained"
                sx={{ mr: "25px" }}
                color="error"
                endIcon={<CloseIcon />}
                onClick={handleLogout}
              >
                Да
              </Button>
              <Button
                variant="contained"
                endIcon={<CheckIcon />}
                onClick={handleClose}
              >
                Нет
              </Button>
            </Box>
          </Box>
        </Box>
      </Modal>
      {responsive && (
        <AppBar
          position="relative"
          sx={{
            boxShadow: "none",
            height: { md: "94px", sm: "90px", xs: "80px" },
            width: "100%",
          }}
        >
          <Toolbar
            sx={{
              background: "#fff",
              justifyContent: "space-between",
              p: { sm: "15px 20px", xs: "10px 20px" },
              position: "fixed",
              width: "100%",
              zIndex: "2",
              borderBottom: "1px solid #EDEDEF",
            }}
          >
            <NavLink to="/">
              <img src={logo} width="113px" alt="" />
            </NavLink>

            {!mobile && (
              <Grid container sx={{ ml: "27px", alignItems: "center" }}>
                <User />
              </Grid>
            )}
            <IconButton
              color="inherit"
              aria-label="open drawer"
              edge="start"
              onClick={handleDrawerToggle}
              sx={{ p: 0 }}
            >
              <i
                className="ri-menu-3-line"
                style={{
                  background: "#FFB951",
                  width: "43px",
                  height: "43px",
                  borderRadius: "50%",
                  color: "#fff",
                  display: "inline-flex",
                  alignItems: "center",
                  justifyContent: "center",
                }}
              />
            </IconButton>
          </Toolbar>
        </AppBar>
      )}
      <Box
        component="nav"
        sx={{
          width: { xl: "300px" },
          flexShrink: { xl: 0 },
          background: "none",
          borderRadius: "0px 20px 20px 0px",
        }}
        aria-label="mailbox folders"
      >
        {responsive && (
          <Drawer
            container={container}
            variant="temporary"
            open={mobileOpen}
            onClose={handleDrawerToggle}
            ModalProps={{
              keepMounted: true,
            }}
            sx={{
              display: { xs: "block", xl: "none" },
              background: "unset",
              "& .MuiDrawer-paper": {
                boxSizing: "border-box",
                width: { md: "300px", sm: "465px", xs: "356px" },
                borderRadius: { xl: 0, xs: "0px 20px 20px 0px" },
              },
            }}
          >
            {drawer}
          </Drawer>
        )}
        {!responsive && (
          <Drawer
            variant="permanent"
            onClose={handleDrawerToggle}
            sx={{
              display: { xs: "none", xl: "block" },
              background: "unset",
              borderRadius: { xl: 0, xs: "0px 20px 20px 0px" },
              "& .MuiDrawer-paper": {
                boxSizing: "border-box",
                width: { md: "300px", sm: "465px", xs: "356px" },
              },
            }}
            open={mobileOpen}
            ModalProps={{
              keepMounted: false,
            }}
          >
            {drawer}
          </Drawer>
        )}
      </Box>
    </>
  );
};

export default AdminSidebar;

const style = {
  position: "absolute",
  top: "50%",
  left: "calc(50% - 15px)",
  transform: "translate(-50%, -50%)",
  boxShadow: "0px 4px 58px rgba(0, 0, 0, 0.15)",
  borderRadius: "15px",
  backgroundColor: "#fff",
  height: "100%",
  width: "calc(100% - 30px)",
  maxWidth: "770px",
  maxHeight: "488px",
  display: "flex",
  alignItems: "center",
  justifyContent: "center",
  margin: "15px",
  padding: "15px",
};
